import { Component, OnInit } from '@angular/core';
import { MyBoardService } from './my-board.service';
import { MyBoard } from './my-board.model';


@Component({
  selector: 'app-my-boards',
  templateUrl: './my-boards.component.html',
  styleUrls: ['./my-boards.component.css']
})
export class MyBoardsComponent implements OnInit {

  myBoards : MyBoard[] = [];

  constructor(private myBoardService:MyBoardService) { }

  ngOnInit() {
    this.getMyBoards();
  }

  getMyBoards() {
    this.myBoardService.getMyBoardList()
    .subscribe(myBoards => this.myBoards = myBoards);
  }

}
