import { Injectable } from '@angular/core';

@Injectable()
export class TopMenuService {

  constructor() {
  }

  getMemberinfo() {
    if(sessionStorage.getItem("member") === null){
      return false;
    }
    return true;
  }

  logout() {
    sessionStorage.removeItem("member");
  }

}



