import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { MyBoard } from './my-board.model';

@Injectable()
export class MyBoardService {
  private myBoardsUrl:string = "https://young-castle-48127.herokuapp.com/seeya/my-boards";

  constructor(private http:Http) { }

  getMyBoardList() : Observable<MyBoard[]> {
    return this.http.get(this.myBoardsUrl).map(this.extractData).catch(this.handleError);
  }

  private extractData(res: Response) {
    console.log('res = ' + JSON.stringify(res));
    // header 정보를 제외한 데이터만 뽑아오는 함수 --> text()
    let json = res.text();
    json = JSON.parse( json);
    return json || [];
  }

  private extractDataForObject(res: Response) {
    console.log('res = ' + JSON.stringify(res));
    let json = res.text();
    console.log(json);
    json = JSON.parse( json);
    return json || {};
  }

  private handleError(res: Response) {
    console.log(res);
    return Observable.throw(res.json().error || 'Server Down');
  }
}



