import { Component, OnInit } from '@angular/core';
import { LoginModalService } from './login-modal.service';
import { Login } from './login.model';



@Component({
  selector: 'app-login-modal',
  templateUrl: './login-modal.component.html',
  styleUrls: ['./login-modal.component.css']
})
export class LoginModalComponent implements OnInit {

  login:Login;
  errorMessage: string;

  constructor(public loginModalService:LoginModalService) { }

  ngOnInit() {

  }

  checkLogin(memberid: string, password: string) {
    this.loginModalService.checkedLogin(memberid, password)
    .subscribe(login => this.login = login, error => this.errorMessage = <any>error);
  }

  onSubmit(f) {
    if(f.valid) {
      var login = f.value;
      this.checkLogin(login.memberid, login.password);
    }
  }
}



