import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Login } from './login.model';


@Injectable()
export class LoginModalService {

  private loginHttpUrl:string = "https://young-castle-48127.herokuapp.com/seeya/login";
  // private loginHttpUrl:string = "http://localhost:8080/seeya/login";
  private headers = new Headers({ 'Content-Type': 'application/json; charset=utf-8' });

  constructor( private http:Http) { }

  checkedLogin(memberid:string, password:string): Observable<Login>{
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });

    let login = {"memberid":memberid, "password":password, "error": ""};
    console.log("login : " + login);
    return this.http.post(this.loginHttpUrl, JSON.stringify(login), options)
    .map(this.extractDataForObject)
    .catch(this.handleError);
  }



  private extractData(res: Response) {
    console.log('res = ' + JSON.stringify(res));
    // header 정보를 제외한 데이터만 뽑아오는 함수 --> text()
    let json = res.text();
    json = JSON.parse(json);
    return json || [];
  }

  private extractDataForObject(res: Response) {
    console.log('res = ' + JSON.stringify(res));
    // header 정보를 제외한 데이터만 뽑아오는 함수 --> text()
    let json = res.text();
    json = JSON.parse(json);
    if(json['error'] != null) {
      alert(json['error']);
    } else {
      sessionStorage.setItem("member", json);
    }
    return json || {};
  }

  private handleError(res: Response) {
    console.log(res);
    return Observable.throw(res.json().error || 'Server Down');
  }
}



